body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Layout{
  display: flex;
}
.Layout .form{
  width:30%;
  padding: 20px 30px;
  min-height: 100vh;
}
.Layout .recetario{
  padding: 22px
}
.recetario {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}
.form h3 {
  font-size: 24px;
  text-align: center;
}
.receta-form input {
  width: 100%
}
.receta-form textarea {
  width: 100%;
  margin-bottom: 10px;
  border:1px solid rgb(214, 214, 214);
}
.receta-form label {
  width: 100%;
  padding: 5px 0 ;
  margin:0;
}
.receta-item {
  border: 1px solid #E6E6E6;
  border-radius: 5px;
  max-width: 300px;
  text-align: center;
  margin: 5px
}
.receta-item h5 {
  margin: 10px 0;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
}
.item-image {
  width: 100%;
  border-radius: 5px 5px 0 0
}
.item-list {
  list-style: none;
  padding: 0;
}
.item-button {
  font-weight: bold;
  padding: 5px 70px;
  background: #f91368;
  color:white;
  letter-spacing: 2px;
  border:none;
  border-radius: 15px;
  cursor: pointer;
  margin: 5px 0 15px 0;
}
.Header {
  height: 70px;
  list-style: none;
  color:white;
  background-image: -webkit-linear-gradient(top left, #fcb900, #fa5f22,#f91368);
  background-image: linear-gradient(to bottom right, #fcb900, #fa5f22,#f91368);
}
.header .wrapper {
  display: flex;
}
.wrapper {
  max-width: 1024px;
  margin: 0 auto;
}
.header-list {
  display: flex;
  justify-content: space-between;
}
.list {
  display: flex;
  align-items: center;
  margin: 0;
  height: 70px;
}
.title{
  font-family: 'Carter One', cursive;
  font-size: 30px;
}

.Footer {
  background-color: black;
  color:white;
  list-style: none;
  padding: 30px 0;
}
.navigate {
  display: flex;
  justify-content: center;
}
.navigate li {
  padding: 10px;
}
.aboutUs {
  display: flex;
}
.aboutUs li {
  padding: 10px;
}
.wrapper {
  max-width: 1024px;
  margin: 0 auto;
}
